.backdrop {
  width: 100%;
  height: 110%;
  z-index: 3;
  backdrop-filter: blur(3px);
  background: #4a10743c;
  transition-property: opacity, visibility;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop--hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.backdrop-search {
  width: 100%;
  height: 100%;
  z-index: 8;
  backdrop-filter: blur(3px);
  background: #000000d9;
  transition-property: opacity, visibility;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop-search--hidden {
