@media (max-width: 1199px) {
  .progress-bar {
    height: 4px;
    transform-origin: 0;
    animation: grow-progress auto linear;
    animation-timeline: scroll();
  }

  .progress-bar__wrap {
    width: 100%;
    animation: hidden-progress auto linear;
    animation-timeline: scroll();
    background-color: #323536;
    position: absolute;
    bottom: 0;
    left: 0;
  }

  @keyframes hidden-progress {
    0% {
      opacity: 0;
    }

    1% {
      opacity: 1;
    }
  }
}

@keyframes grow-progress {
  from {
    background-color: var(--accent-text-color);
    transform: scaleX(0);
  }

  to {
    background-color: var(--accent-text-color);
    transform: scaleX(1);
  }
}

